#home header {
    min-height: 100vh;
    min-width: 100vw;
    overflow: hidden;
}



/* ========== CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
    color: white;
}

/* ========= HEADER SOCIALS */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 30rem;
    bottom: 20rem;
    width: 50px;
    border: 1px solid rgba(255, 255, 255, 0.303);
    padding: 10px;
    border-radius: 1rem ;
    background-color: var(--color-bg-variant);
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

.header__socials::after {
    content: '';
    width: 2px;
    height: 2rem;
    background: white;
}

/* =========== ME */

.me {
    display: flex;
    margin: auto;
    justify-content: center;
}
.me img{
    
    background-color: black;
    width: 49rem;
    height: 30rem;
    margin-top: 2rem;
    border-radius: 2rem  ;
    border: 1px solid rgba(235, 235, 235, 0.36);
    box-shadow:rgba(0, 0, 0, 0.3) 0px 19px 10px, rgba(0, 0, 0, 0.15) 0px 15px 12px;
}

/* ==== SCROLL DOWN */

.scroll__down {
    position: absolute;
    right:30rem;
    bottom: 21rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 600;
    font-size: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.303);
    padding: 10px;
    border-radius: 1rem ;
    background-color: var(--color-bg-variant);
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

.introductionBanner{
    display: flex;
    flex-direction: column;
    padding: 10px;
    border-radius: 5rem;
    background-color: var(--color-bg-variant);
    width: 50%;
    text-align: center;
}

.nameDeco {
    color: white;
	font-size: 60px;
	text-shadow: -1px 1px 2px #000,
				  1px 1px 2px #000,
				  1px -1px 0 #000,
				  -1px -1px 0 #000;
}

._socialScroll{
    display: flex;
}


/* ====== MEDIA QUERIES ==== */

/* LARGER DESKTOPS AND MONITORS */
@media all and (min-width:1201px)  and (max-width:1880px) {
    .scroll__down{
        position: absolute;
        right:10rem;
        bottom: 10rem;
    }
    .header__socials {
        position: absolute;
        left:12rem; 
        bottom: 10rem;
    }
}

@media all and (min-width:1024px)  and (max-width:1200px) {
    .scroll__down{
        position: absolute;
        right:4rem;
        bottom: 10rem;
    }
    .header__socials {
        position: absolute;
        left:5rem; 
        bottom: 10rem;
    }
}


/* LAPTOPS, LARGER TABLETS IN LANDSCAPE, AND SMALL DESKTOPS */
@media all and (min-width:769px) and (max-width:1023px) {
    .scroll__down{
        display: none;
    }
    .header__socials {
        display: none;
    }
}

/* TABLETS & LARGER SMARTPHONES */
@media all and (min-width:481px) and (max-width:768px) {
    .introductionBanner{
        margin-top: 20px;
        border-radius: 2rem;
    }
    .me img{
        background-color: black;
        width: 40%;
        height: 40%;
        margin-top: 2rem;
        border-radius: 1rem  ;
        border: 1px solid rgba(235, 235, 235, 0.36);
        box-shadow:rgba(0, 0, 0, 0.3) 0px 19px 10px, rgba(0, 0, 0, 0.15) 0px 15px 12px;
    }
    .scroll__down{
        display: none;
    }
    .header__socials {
        display: none;
    }
}

/* SMALLER SMARTPHONES */
@media all and (min-width:320px) and (max-width:480px) {
    .introductionBanner{
        margin-top: 20px;
        border-radius: 2rem;
    }
    .me img{
        background-color: black;
        width: 40%;
        height: 40%;
        margin-top: 2rem;
        border-radius: 1rem  ;
        border: 1px solid rgba(235, 235, 235, 0.36);
        box-shadow:rgba(0, 0, 0, 0.3) 0px 19px 10px, rgba(0, 0, 0, 0.15) 0px 15px 12px;
    }
    .scroll__down{
        display: none;
    }
    .header__socials {
        display: none;
    }
} 
